.CreateBenefitPage {
	padding: 20px;
	background-color: rgb(250, 250, 250);

	.title {
		font-size: 2rem;
		align-items: flex-start;
	}

	.sub-title {
		font-size: 1.5rem;
	}

	.methods-container {
		padding-left: 16px !important;
		padding-right: 16px !important;

		.methods-container-item-container {
			border-radius: 5px;
			background-color: #dddfe3;
		}
	}

	.arrays-container {
		padding-left: 16px !important;
		padding-right: 16px !important;

		.arrays-container-item-container {
			border-radius: 5px;
			background-color: #dddfe3;
			padding: 3px !important;
		}
	}

	.data-field-remove-btn {
		margin-bottom: 4px;
	}

	.create-benefit-btn-container {
		margin-top: 20px;
	}

	.text-btn {
		text-transform: none;
		font-weight: 500;
		font-size: 15px;
	}

	.benefit-tree {
		margin-left: 40px;
	}

	.loading-indicator {
		color: green[500];
		justify-content: 'center';
        margin: auto;
        z-index: -1;
	}
}

.pdf__container{
  margin:0 auto;
}

